import { useHistory } from "react-router-dom";
import { useEffect, useState } from 'react'
import { useParams } from "react-router";
import React from 'react';
import SaveIcon from '@material-ui/icons/Save';
// import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import {
  Avatar,
  Box,
  Button,
  Card,
  CardContent,
  Container,
  Grid,
  Divider,
  Grow,
  Link,
  List,
  IconButton,
  ListItem,
  ListItemText,
  ListItemAvatar,
  TextField,
  Typography,
} from '@mui/material';
import { withStyles } from '@mui/styles';

import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';

import { getDetailsGuild, setRole } from '../../api';
import SideHeadBar from '../../components/SideHeadbar';

const styles = {
  formControl: {
    marginBottom: '20px'
  },

  card: {
    minWidth: '200px',
    maxWidth: '350px',
    display: 'flex',
    justifyContent: 'center'
  },

  noRole: {
    fontSize: '30px'
  }
}


function Role(props) {
  const [roleInfo, setRoleInfo] = useState({
    name: '',
    price: '',
    description: ''
  });
  const [checked, setChecked] = useState(false);
  const [roles, setRoles] = useState([]);
  const { classes } = props;
  const { id } = useParams();
  useEffect(() => {
    getDetailsGuild(id).then(result => {
      setRoles(result.data.roles);
    })
  }, []);

  const handleSaveRole = () => {
    setRole(id, roleInfo).then(result => {
      console.log("result", result);
    });
  }
  return (
    <>
      <SideHeadBar />
      <Container maxWidth="lg" >

        {
          roles.length == 1 ?
            <Grid
              container
              justifyContent="center"
              alignItems="center">
              <span className={classes.noRole}>No Role</span>
            </Grid>
            :
            <Grid container spacing={5}>
              <Grid item xs={12} sm={12} md={6} lg={6}>
                <Grid container>
                  <Grid item >
                    <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                      <ListItem alignItems="flex-start"
                        secondaryAction={
                          <IconButton edge="end" aria-label="delete">
                            <EditIcon />
                          </IconButton>
                        }>
                        <ListItemAvatar>
                          <Avatar alt="Remy Sharp" src="/assets/image/avatar.jpg" />
                        </ListItemAvatar>
                        <ListItemText
                          primary="Brunch this weekend?"
                          secondary={
                            <React.Fragment>
                              <Typography
                                sx={{ display: 'inline' }}
                                component="span"
                                variant="body2"
                                color="text.primary"
                              >
                                Ali Connors
                              </Typography>
                              {" — I'll be in your neighborhood doing errands this…"}
                            </React.Fragment>
                          }
                        />
                      </ListItem>
                      <Divider variant="inset" component="li" />
                      <ListItem alignItems="flex-start">
                        <ListItemAvatar>
                          <Avatar alt="Travis Howard" src="/assets/image/avatar.jpg" />
                        </ListItemAvatar>
                        <ListItemText
                          primary="Summer BBQ"
                          secondary={
                            <React.Fragment>
                              <Typography
                                sx={{ display: 'inline' }}
                                component="span"
                                variant="body2"
                                color="text.primary"
                              >
                                to Scott, Alex, Jennifer
                              </Typography>
                              {" — Wish I could come, but I'm out of town this…"}
                            </React.Fragment>
                          }
                        />
                      </ListItem>
                      <Divider variant="inset" component="li" />
                      <ListItem alignItems="flex-start">
                        <ListItemAvatar>
                          <Avatar alt="Cindy Baker" src="/assets/image/avatar.jpg" />
                        </ListItemAvatar>
                        <ListItemText
                          primary="Oui Oui"
                          secondary={
                            <React.Fragment>
                              <Typography
                                sx={{ display: 'inline' }}
                                component="span"
                                variant="body2"
                                color="text.primary"
                              >
                                Sandra Adams
                              </Typography>
                              {' — Do you have Paris recommendations? Have you ever…'}
                            </React.Fragment>
                          }
                        />
                      </ListItem>
                    </List>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12} sm={12} md={6} lg={6}>
                <Card
                  className={classes.card}
                  style={{ border: '1px solid green' }}>
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                      Your Public Checkout Link
                    </Typography>
                    <TextField
                      fullWidth
                      id="outlined-size-small"
                      size="sm"
                      style={{ marginBottom: '20px' }}
                    />
                    <Button
                      fullWidth
                      style={{ marginBottom: '20px' }}
                      variant="contained"
                      color="success">
                      View your public store
                    </Button>
                    <Typography style={{ marginBottom: '20px' }} variant="body1" color="text.secondary">
                      Share this link anywhere. This is also the link you place on your
                      <Link href="#" underline="none">
                        {' Medal profile. '}
                      </Link>
                      Users who buy donations are not required to be in your server.
                    </Typography>
                    <Typography gutterBottom variant="h5" component="div">
                      Donations inside Discord
                    </Typography>
                    <Typography gutterBottom variant="body1" component="div">
                      The roles on the left sync directly with your
                      <Link href="#" underline="none">
                        {' Discord Roles '}
                      </Link>
                      . To get started, price your roles and set a PayPal email in settings.
                    </Typography>
                    <Typography gutterBottom variant="body1" component="div">
                      Your users can type <strong>"donate"</strong>  in your Discord Server, they will be sent your donate link.
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>
        }
      </Container>

    </>
  );
}

export default withStyles(styles)(Role)